import { _decorator, Component, Node, Vec3, Collider2D, Contact2DType, IPhysics2DContact,  } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Item')
export class Item extends Component {
    public bulletSpeed: number = 10;
    private posX: number;
    private posY: number;
    public selfCollider: Collider2D; 
    public otherCollider: Collider2D;
    start() {
        let collider = this.getComponent(Collider2D);
        if(collider){
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
        this.selfCollider.group = 6;
    }
    onBeginContact( contact: IPhysics2DContact | null){
        
        console.log(this.selfCollider.group, this.otherCollider.group);
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
    update(deltaTime: number) {
        this.posX = this.node.position.x;
        this.posY = this.node.position.y;
        if(this.posX === 0){
            return;
        }
        // this.posY -= this.bulletSpeed * 0.3;
        
        let posBullet = new Vec3(this.posX, this.posY);
        this.node.setPosition(posBullet);
    }
}


